import { useState } from "react";
import styles from "./SecondScreen.module.css";
import Modal from "react-modal";
const SecondScreen = () => {
  const [loading, setLoading] = useState(null);
  const [data, setData] = useState(null);
  const [value, setValue] = useState("");
  const [openModule, setOpenModule] = useState(false);
  const [warning, setWarning] = useState("");
  const inputHandler = (e) => {
    setValue(e.target.value);
  };
  const exactDate = `https://api.nasa.gov/planetary/apod?api_key=E2yibYfhl3owla9uHSeARmak9Hk6hChjrYnZjIPe&date=${value}&concept_tags=True`;
  const getExactDate = async () => {
    try {
      setLoading(true);
      const response = await fetch(exactDate);
      const data = await response.json();
      setData(data);
      setLoading(false);
      if (data.code) {
        setOpenModule(false);
        setWarning(
          "Date must be after 1995-06-16, the first day an APOD picture was posted."
        );
      } else {
        setWarning("");
        setOpenModule(true);
      }
    } catch (e) {
      console.log(e);
    }
  };
  const closeModal = () => {
    setOpenModule(false);
  };

  return (
    <div className="slide two">
      <div className={styles.getData}>
        <h4 className={styles.about}>
          Here you can get the Astronomy Picture of the Day
        </h4>
        <p className={styles.warning}>{warning}</p>
        <div className={styles.dateAndButton}>
          <input
            min="1995-06-16"
            onChange={inputHandler}
            value={value}
            className={styles.inputDiv}
            placeholder="YYYY-MM-DD"
            type="date"
          />
          <button className={styles.btn} onClick={getExactDate}>
            Get the Image of the Day
          </button>
        </div>
      </div>
      {loading ? (
        <div className={styles.loader}>
          <div className="spinner-border text-danger" role="status"></div>
        </div>
      ) : null}
      {!openModule ? null : (
        <Modal
          ariaHideApp={false}
          onRequestClose={closeModal}
          isOpen={openModule}
        >
          <div className={styles.data}>
            <div onClick={closeModal} className={styles.close}>
              <i className="fas fa-times"></i>
            </div>
            <h3 className={styles.title}>{data.title}</h3>
            <div className={styles.date}>{data.date}</div>
            <div className={styles.imageAndAbout}>
              <div className={styles.explanationDiv}>
                <h6 className={styles.explanation}>{data.explanation}</h6>
              </div>
              <div className={styles.imageDiv}>
                <img className={styles.image} src={data.hdurl} alt="/" />
              </div>
            </div>
          </div>
        </Modal>
      )}
    </div>
  );
};
export default SecondScreen;
